import React, { useState } from "react";
import { Link } from "react-router-dom";
import styles from "./mymedicheck.module.css";
import axios from "axios";
const Mymedicheck = () => {
  const [height, setHeight] = useState("");
  const [weight, setWeight] = useState("");
  const [heightError, setHeightError] = useState("");
  const [weightError, setWeightError] = useState("");
  const [waist, setWaist] = useState("");
  const [waistInch, setWaistInch] = useState("");
  const [bmi, setBmi] = useState("");
  const [systolicBP, setSystolicBP] = useState("");
  const [diastolicBP, setDiastolicBP] = useState("");
  const [bloodSugar, setBloodSugar] = useState("");
  const [ast, setAst] = useState("");
  const [alt, setAlt] = useState("");
  const [ggtp, setGgtp] = useState("");
  const [proteinuria, setProteinuria] = useState("");
  const [hemoglobin, setHemoglobin] = useState("");
  const [serumCreatinine, setSerumCreatinine] = useState("");
  // const [gfr, setGfr] = useState("");
  const [ageGroup, setAgeGroup] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const [smoking, setSmoking] = useState("1");
  const [drinking, setDrinking] = useState("1");

  //
  const handleAgeGroupChange = (e) => {
    const value = e.target.value;
    setAgeGroup(value);
    if (value === "" || value % 5 === 0) {
      setErrorMessage("");
    } else {
      setErrorMessage(" 5  .");
    }
  };

  const handleBlur = () => {
    if (ageGroup && ageGroup % 5 !== 0) {
      setErrorMessage(" 5  .");
    } else {
      setErrorMessage("");
    }
  };
  //

  //
  const [selectedGender, setSelectedGender] = useState("1"); //   

  const handleGenderChange = (event) => {
    setSelectedGender(event.target.value);
  };
  //

  //, 
  const handleHeightChange = (e) => {
    const value = e.target.value;
    setHeight(value);
    if (value === "" || value % 5 === 0) {
      setHeightError("");
    } else {
      setHeightError(" 5  .");
    }
  };

  const handleWeightChange = (e) => {
    const value = e.target.value;
    setWeight(value);
    if (value === "" || value % 5 === 0) {
      setWeightError("");
    } else {
      setWeightError(" 5  .");
    }
  };

  const handleHeightBlur = () => {
    if (height && height % 5 !== 0) {
      setHeightError(" 5  .");
    } else {
      setHeightError("");
    }
  };

  const handleWeightBlur = () => {
    if (weight && weight % 5 !== 0) {
      setWeightError(" 5  .");
    } else {
      setWeightError("");
    }
  };
  //

  const isNumber = (value) => !isNaN(value);

  const calculateBmi = () => {
    if (height && weight) {
      const heightInMeters = height / 100;
      const bmiValue = (weight / (heightInMeters * heightInMeters)).toFixed(2);
      setBmi(bmiValue);
    }
  };

  const convertWaistToInch = (cm) => {
    const inch = (cm / 2.54).toFixed(2);
    setWaistInch(inch);
  };

  const handleSubmit = async () => {
    //  
    const ageForServer = ageGroup ? parseInt(ageGroup, 10) : null;
    const genderForServer = selectedGender === "1" ? 1 : 2;
    const bmiForServer = bmi ? parseFloat(bmi) : null; //  
    const waistForServer = waist ? parseFloat(waist) * 2 : null; //   2
    const smokingForServer = smoking === "1" ? 1 : 0; //  
    const drinkingForServer = drinking === "1" ? 1 : 0; //  

    //   
    const data = {
      age: ageForServer,
      sex: genderForServer,
      bmi: bmiForServer,
      waist: waistForServer,
      systolicBloodPressure: systolicBP ? parseInt(systolicBP, 10) : null,
      diastolicBloodPressure: diastolicBP ? parseInt(diastolicBP, 10) : null,
      fastingBloodGlucose: bloodSugar ? parseFloat(bloodSugar) : null,
      ast: ast ? parseFloat(ast) : null,
      alt: alt ? parseFloat(alt) : null,
      ggtp: ggtp ? parseFloat(ggtp) : null,
      serumCreatinine: serumCreatinine ? parseFloat(serumCreatinine) : null,
      hemoglobin: hemoglobin ? parseFloat(hemoglobin) : null,
      smoke: smokingForServer,
      drink: drinkingForServer,
      proteinuria: proteinuria ? parseFloat(proteinuria) : null //  
    };

    try {
      const response = await axios.post('/api/predict-data', data, {
        headers: {
          'Content-Type': 'application/json',
        }
      });

      if (response.status === 201) {
        alert(".");
      } else {
        alert(" .   .");
      }
    } catch (error) {
      console.error('Error:', error);
      alert("   .");
    }
  }


    return (
      <div className={styles.container}>
        <div className={styles.sectionLeft}>
          <h2 className={styles.side}> </h2>
          <div id="line">
            <ul>
              <li>
                <Link
                    to="/MedicalInform"
                    style={{ textDecoration: "none" }}
                    className="link"
                >
                  - 
                </Link>
              </li>
              <li>
                <Link
                    to="/Prediction"
                    style={{ textDecoration: "none" }}
                    className="link"
                >
                  -  
                </Link>
              </li>
              <li>
                <Link
                    to="/SearchHospital"
                    style={{ textDecoration: "none" }}
                    className="link"
                >
                  -  
                </Link>
              </li>
            </ul>
          </div>
        </div>
        <div className={styles.inner}>
          <h2 className={styles.title}>  </h2>
          <div class="standard-data col-2">
            <div className={styles.topline}>
              <div className={styles.dataBox}>
                <div className={styles.inputGroup}>
                  <span className={styles.titleTop}></span>
                  <input
                      type="number"
                      className={styles.tiA1}
                      id="ageGroup"
                      name="ageGroup"
                      value={ageGroup}
                      onChange={handleAgeGroupChange}
                      onBlur={handleBlur}
                      title=""
                      // placeholder=" 5 "
                  />
                  <span className={styles.cham}></span>
                </div>
                {errorMessage && (
                    <div
                        style={{
                          color: "red",
                          fontSize: "14px",
                          marginLeft: "30px",
                          marginBottom: "10px",
                        }}
                    >
                      {errorMessage}
                    </div>
                )}
                <div className={styles.textContainer_top}>
                   0~2 : 0 / 3~7 : 5 / 8~12 : 10  .
                </div>
              </div>

              <div className={styles.dataBox}>
                {/* <span className={styles.titleTop}></span> */}
                <div className={styles.genderGroup}>
                <span className={styles.bgchk1}>
                  {/* <label htmlFor="gender_1"></label> */}
                  <input
                      className={styles.radiog1}
                      type="radio"
                      id="gender_1"
                      name="gender"
                      value="1"
                      checked={selectedGender === "1"}
                      onChange={handleGenderChange}
                  />
                  <label htmlFor="gender_1"></label>
                </span>
                  <span className={styles.bgchk2}>
                  {/* <label htmlFor="gender_2"></label> */}
                    <input
                        className={styles.radiog2}
                        type="radio"
                        id="gender_2"
                        name="gender"
                        value="2"
                        checked={selectedGender === "2"}
                        onChange={handleGenderChange}
                    />
                  <label htmlFor="gender_2"></label>
                </span>
                </div>
              </div>
            </div>
          </div>
          <div className={styles.out}>
            <div className={styles.title__1}> </div>

            <div className={styles.outline}>
              <div
                  className="row-table v1 box-in-table"
                  data-title="  "
              >
                <table className={styles.title__table1}>
                  <tbody className={styles.tbody_1}>
                  <tr>
                    <td className={styles.title__1_a}>
                      <div className={styles.textContainer}>
                         , ,    .
                      </div>
                      <div className={styles.inputContainer}>
                        <p className={styles.inputtext1}></p>
                        <input
                            type="text"
                            className={styles.ti_a1_1}
                            name="heightVl"
                            maxLength="5"
                            // placeholder=" ."
                            title=" "
                            value={height}
                            onChange={handleHeightChange}
                            // onBlur={handleHeightBlur}
                            onBlur={calculateBmi}
                        />
                        <em className="dash ml10">cm</em>
                        {heightError && (
                            <div className={styles.error}>{heightError}</div>
                        )}
                      </div>
                    </td>
                    <td className={styles.title__1_a}>
                      <div className={styles.inputContainer_c}>
                        <p className={styles.inputtext2}></p>
                        <input
                            type="text"
                            className={styles.ti_a1_1}
                            name="weightVl"
                            maxLength="5"
                            // placeholder=" ."
                            title=" "
                            value={weight}
                            onChange={handleWeightChange}
                            // onBlur={handleWeightBlur}
                            onBlur={calculateBmi}
                        />
                        <em className="dash ml10">kg</em>
                        {weightError && (
                            <div className={styles.error2}>{weightError}</div>
                        )}
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td className={styles.title__1_a}>
                      <div className={styles.inputContainer}>
                        <p className={styles.label_top}></p>
                        <input
                            type="text"
                            className={styles.ti_a1_1}
                            name="waistVl"
                            maxLength="5"
                            // placeholder=" ."
                            title=" "
                            value={waist}
                            onChange={(e) => setWaist(e.target.value)}
                            onBlur={() => convertWaistToInch(waist)}
                        />
                        <em className="dash ml10"> cm</em>
                        <input
                            type="text"
                            className={styles.ti_a1_1}
                            readOnly
                            maxLength="3"
                            title=" (inch)"
                            value={waistInch}
                            style={{ marginLeft: "20px", width: "70px" }}
                        />
                        <em className="dash ml10"> inch</em>
                      </div>
                    </td>
                    <td className={styles.title__1_a}>
                      <div className={styles.inputContainer}>
                        <p className={styles.pb10}> (BMI)</p>
                        <input
                            type="text"
                            className={styles.ti_a1_1}
                            readOnly
                            title=" (BMI) "
                            value={bmi}
                        />
                        <em className="dash ml10"> kg/m</em>
                      </div>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>

            <div className={styles.title__2}>   </div>

            <div className="cont-area">
              <div
                  className="row-table v1 box-in-table"
                  data-title="    "
              >
                <table>
                  <tbody>
                  <tr>
                    <div className={styles.textContainer1}>
                       , ,      .
                    </div>
                  </tr>
                  <tr>
                    <td>
                      <div className="form-wrap box-in-table">
                        <p className={styles.tit}></p>
                        <div className={styles.inputContainer2}>
                          <p className={styles.label}>  </p>
                          <input
                              type="text"
                              className={styles.ti_a1_1}
                              id="systcBpVl"
                              name="systcBpVl"
                              maxLength="3"
                              title="  "
                              value={systolicBP}
                              onChange={(e) => setSystolicBP(e.target.value)}
                          />
                          <em className="inline-block">mmHg</em>
                          <span className={styles.cham}>
                            (  : 120 )
                          </span>
                        </div>
                        <div className={styles.inputContainer2}>
                          <p className={styles.label}>  </p>
                          <input
                              type="text"
                              className={styles.ti_a1_1}
                              id="distcBpVl"
                              name="distcBpVl"
                              maxLength="3"
                              title="  "
                              value={diastolicBP}
                              onChange={(e) => setDiastolicBP(e.target.value)}
                          />
                          <em className="inline-block">mmHg</em>
                          <span className={styles.cham}>
                            (  : 80 )
                          </span>
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div className="form-wrap box-in-table">
                        <p className={styles.tit}></p>
                        <div className={styles.inputContainer2}>
                          <p className={styles.label}>  2 </p>
                          <input
                              type="text"
                              className={styles.ti_a1_1}
                              id="empstoBdsgVl"
                              name="empstoBdsgVl"
                              maxLength="3"
                              title=" "
                              value={bloodSugar}
                              onChange={(e) => setBloodSugar(e.target.value)}
                          />
                          <em className="inline-block">mmHg</em>
                          <span className={styles.cham}>
                            (  : 120 )
                          </span>
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div className="form-wrap box-in-table">
                        <p className={styles.tit}></p>
                        <div className={styles.inputContainer2}>
                          <p className={styles.label}>  (AST) </p>
                          <input
                              type="text"
                              className={styles.ti_a1_1}
                              id="ast"
                              name="ast"
                              maxLength="3"
                              title=" (AST) "
                              value={ast}
                              onChange={(e) => setAst(e.target.value)}
                          />
                          <em className="inline-block">mg/dL</em>
                          <span className={styles.cham}>
                            (  : 40 )
                          </span>
                        </div>
                        <div className={styles.inputContainer2}>
                          <p className={styles.label}> (ALT)</p>
                          <input
                              type="text"
                              className={styles.ti_a1_1}
                              id="alt"
                              name="alt"
                              maxLength="3"
                              title="LDL- "
                              value={alt}
                              onChange={(e) => setAlt(e.target.value)}
                          />
                          <em className="inline-block">mg/dL</em>
                          <span className={styles.cham}>
                            (  : 50 )
                          </span>
                        </div>
                        <div className={styles.inputContainer2_1}>
                          <p className={styles.label}>  (-GTP)</p>
                          <input
                              type="text"
                              className={styles.ti_a1_1}
                              id="ggtp"
                              name="ggtp"
                              maxLength="3"
                              title=" "
                              value={ggtp}
                              onChange={(e) => setGgtp(e.target.value)}
                          />
                          <em className="inline-block">mg/dL</em>
                          <sapn className={styles.cham}>
                            (  :  11~63 /  8~35)
                          </sapn>
                        </div>
                      </div>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div id="medi_request_03" className={styles.title__3}>
              ,    
            </div>
            <div class="cont-area">
              <div
                  class="row-table v1 box-in-table"
                  data-title=",    "
              >
                <table>
                  <tbody>
                  <tr>
                    <div className={styles.textContainer1}>
                             .
                    </div>
                  </tr>
                  <tr>
                    <td>
                      <div class="form-wrap box-in-table">
                        <p class={styles.tit}></p>
                        <div className={styles.inputContainer2}>
                          <p class={styles.label}> </p>
                          <select
                              class="w190px"
                              className={styles.ti_a1_1}
                              name="proteinuria"
                              id="proteinuria"
                              title="  "
                          >
                            <option value=""></option>
                            <option value="1">1(30mg/dL )</option>
                            <option value="2">2(100mg/dL )</option>
                            <option value="3">3(300mg/dL )</option>
                            <option value="4">4(1000mg/dL )</option>
                          </select>
                          <span className={styles.cham}>
                            (  : 30)
                          </span>
                        </div>

                        <div className={styles.inputContainer2}>
                          <p class={styles.label}> </p>
                          <input
                              type="text"
                              class={styles.ti_a1_1}
                              id="serumCreatinine"
                              name="serumCreatinine"
                              maxlength="3"
                              title=" "
                              value={serumCreatinine}
                              onChange={(e) => setSerumCreatinine(e.target.value)}
                          />
                          <em class="inline-block">mg/dL</em>

                          <span className={styles.cham}>
                            (  : 1.5 )
                          </span>
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div class="form-wrap box-in-table">
                        <p class={styles.tit}> </p>
                        <div className={styles.inputContainer2}>
                          <p class={styles.label}> </p>
                          <input
                              type="text"
                              className={styles.ti_a1_1}
                              id="hemoglobin"
                              name="hemoglobin"
                              maxlength="4"
                              title=" "
                              value={hemoglobin}
                              onChange={(e) => setHemoglobin(e.target.value)}
                          />
                          <em class="inline-block">g/dL</em>
                          <span className={styles.cham}>
                            (  :  13~16.5 /  12~15.5)
                          </span>
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <div class="form-wrap box-in-table">
                        <div className={styles.inputRow}>
                          <div className={styles.inputContainer2_bottom}>
                            <div className={styles.labelAndRadios}>
                              <p className={styles.label_s}>  </p>
                              <div className={styles.radioGroup1}>
                                <label className={styles.radioLabel_y}>
                                  <input
                                      type="radio"
                                      name="smoking"
                                      value="1"
                                      checked={smoking === "1"}
                                      onChange={(e) => setSmoking(e.target.value)}
                                  />
                                  
                                </label>
                                <label className={styles.radioLabel_n}>
                                  <input
                                      className={styles.smokbtn}
                                      type="radio"
                                      name="smoking"
                                      value="2"
                                      checked={smoking === "2"}
                                      onChange={(e) => setSmoking(e.target.value)}
                                  />
                                  
                                </label>
                              </div>
                            </div>
                          </div>
                          <div className={styles.inputContainer2_bottom}>
                            <div className={styles.labelAndRadios}>
                              <p className={styles.label_d}>  </p>
                              <div className={styles.radioGroup2}>
                                <label className={styles.radioLabel_y}>
                                  <input
                                      className={styles.drinkbtn}
                                      type="radio"
                                      name="drinking"
                                      value="1"
                                      checked={drinking === "1"}
                                      onChange={(e) =>
                                          setDrinking(e.target.value)
                                      }
                                  />
                                  
                                </label>
                                <label className={styles.radioLabel_n}>
                                  <input
                                      type="radio"
                                      name="drinking"
                                      value="2"
                                      checked={drinking === "2"}
                                      onChange={(e) =>
                                          setDrinking(e.target.value)
                                      }
                                  />
                                  
                                </label>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <button className={styles.registerButton} onClick={handleSubmit}>
            
          </button>
        </div>
      </div>
  );
};

export default Mymedicheck;