import React, { useState, useEffect } from "react";
import "../pages/email.css";
import { Link, useLocation } from "react-router-dom";
import Useagree from "../pages/Useagree";
// import axios from "axios";

function Email() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [birthdate, setBirthdate] = useState("");
  const [gender, setGender] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [termsAccepted, setTermsAccepted] = useState(false);
  const [privacyAccepted, setPrivacyAccepted] = useState(false);
  const [marketingAccepted, setMarketingAccepted] = useState(false);
  const [allAccepted, setAllAccepted] = useState(false);
  const [errors, setErrors] = useState({});
  const location = useLocation();

  const [isTermsModalOpen, setIsTermsModalOpen] = useState(false);

  const isEmailVerified = localStorage.getItem("isEmailVerified") === "true";
  useEffect(() => {
    if (location.state && location.state.isEmailVerified) {
      localStorage.setItem("isEmailVerified", "true");
    }
  }, [location.state]);

  //  
  const validatePassword = (password) => {
    const passwordRegex =
      /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{8,20}$/;
    return passwordRegex.test(password);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const newErrors = {};

    if (!validatePassword(password)) {
      newErrors.password =
        " , ,   8~20 .";
    }

    if (password !== confirmPassword) {
      newErrors.confirmPassword = "  .";
    }

    if (!termsAccepted) {
      newErrors.termsAccepted = "  .";
    }
    if (!privacyAccepted) {
      newErrors.privacyAccepted = "     .";
    }

    if (!isEmailVerified) {
      newErrors.email = "  .";
    }
    setErrors(newErrors);

    //   if (Object.keys(newErrors).length === 0) {
    //     //   
    //     try {
    //       const response = await axios.post("/api/signup", {
    //         name,
    //         email,
    //         password,
    //         termsAccepted,
    //         marketingAccepted,
    //       });
    //       //    
    //       console.log(response.data);
    //       alert(" .");
    //     } catch (error) {
    //       //    
    //       console.error(error);
    //       setErrors({ submit: "   ." });
    //     }
    //   }
  };

  const handleAllAcceptedChange = (e) => {
    const { checked } = e.target;
    setAllAccepted(checked);
    setTermsAccepted(checked);
    setPrivacyAccepted(checked);
    setMarketingAccepted(checked);
  };
  return (
    <div className="email-signup-container">
      <h2> </h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label></label>
          <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="  "
            required
          />
        </div>
        <div className="form-inline-group">
          <div className="form-group">
            <label></label>
            <div style={{ display: "flex" }}>
              <label>
                <input
                  type="radio"
                  value="male"
                  checked={gender === "male"}
                  onChange={(e) => setGender(e.target.value)}
                />
                
              </label>
              <label>
                <input
                  type="radio"
                  value="female"
                  checked={gender === "female"}
                  onChange={(e) => setGender(e.target.value)}
                />
                
              </label>
            </div>
          </div>

          <div className="form-group birthdate">
            <label></label>
            <input
              type="date"
              value={birthdate}
              onChange={(e) => setBirthdate(e.target.value)}
              required
            />
          </div>
        </div>

        <div className="form-group">
          <label></label>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder=" "
            required
          />
          <Link
            to={{ pathname: "/signup/email-verification", state: { email } }}
            className="verify-button"
          >
             
          </Link>
        </div>
        <div className="form-group">
          <label></label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder=", ,   8~20"
            required
          />
          {errors.password && <p className="error">{errors.password}</p>}
        </div>
        <div className="form-group">
          <label> </label>
          <input
            type="password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            placeholder="  "
            required
          />
          {errors.confirmPassword && (
            <p className="error">{errors.confirmPassword}</p>
          )}
        </div>
        <div className="form-group">
          <label style={{ fontWeight: "bold" }}>
            <input
              type="checkbox"
              checked={allAccepted}
              onChange={handleAllAcceptedChange}
              required
            />
             
          </label>
        </div>
        <div className="form-group">
          <label
            onClick={() => setIsTermsModalOpen(true)}
            style={{ cursor: "pointer", textDecoration: "underline" }}
          >
            <input
              type="checkbox"
              checked={termsAccepted}
              onChange={(e) => setTermsAccepted(e.target.checked)}
            />
            [] 
          </label>
          {errors.termsAccepted && (
            <p className="error">{errors.termsAccepted}</p>
          )}
        </div>
        <div className="form-group">
          <label
            onClick={() => setIsTermsModalOpen(true)}
            style={{ cursor: "pointer", textDecoration: "underline" }}
          >
            <input
              type="checkbox"
              checked={privacyAccepted}
              onChange={(e) => setPrivacyAccepted(e.target.checked)}
              required
            />
            []     
          </label>
          {errors.privacyAccepted && (
            <p className="error">{errors.privacyAccepted}</p>
          )}
        </div>

        <div className="form-group">
          <label>
            <input
              type="checkbox"
              checked={marketingAccepted}
              onChange={(e) => setMarketingAccepted(e.target.checked)}
            />
            []     
          </label>
        </div>
        <button type="submit" className="submit-button">
          
        </button>
      </form>

      <Useagree
        isOpen={isTermsModalOpen}
        onClose={() => setIsTermsModalOpen(false)}
      />
    </div>
  );
}

export default Email;
